body{
    background-color: #f1f5f8;
}
.card {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;
    height: 1075px;
}

.card:hover {
    box-shadow: 0 4px 8px 0 black;
}

.New{
    border-radius: 100%;
    bottom: 25px;
    font-size: 25px;
    padding: 20px;
    position: fixed;
    right: 25px;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    transition: 0.3s;

}

.New:hover {
    box-shadow: 0 4px 8px 0 black; 
}

.button-container {
    display: flex;
    flex-direction: column-reverse; /* Invierte el orden de los botones */
    position: fixed;
    right: 25px;
    bottom: 25px;
    gap: 15px; /* Espaciado entre botones */
}


